.header {
    width: 100%;
    position: absolute;
    top: 0;
    z-index: 10;

    &__container {
        display: flex;
        max-width: 1200px;
        width: 100%;
        margin: 0 auto;
        padding: 0 20px;
        justify-content: space-between;
        align-items: center;
        height: 98px;
    }

    &__logo {
        font-weight: bold;
        font-size: 36px;
        line-height: 48px;
        color: #4579CB;
    }

    &__link {
        max-width: 1000px;
        width: 100%;
    }

    &__list {
        display: flex;
        justify-content: flex-end;
        margin: 0 13px;
    }

    &__item {
        margin: 0 22px;

        a {
            font-weight: bold;
            font-size: 16px;
            line-height: 24px;
            color: #666666;
        }
    }

    &__button {
        max-width: 170px;
        width: 100%;
        background-color: #4579CB;
        border-radius: 8px;
        padding: 13px 25px;
    }

    &__button_text {
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF;
        padding: 30px 5px;
    }
}

