@media only screen and (max-width: 768px) {
    body.lock {
        overflow: hidden;
    }

    .header__container {
        justify-content: space-between;
    }

    .header__link {
        width: 30px;
    }

    .header__list {
        display: block;
        position: fixed;
        top: 80px;
        right: -200%;
        height: 100%;
        width: 180px;
        background-color: #4579cb;
        opacity: 0.9;
        transition: all 0.3s ease 0s;
        z-index: 5;
    
        .header__item {
            border-bottom: 1px solid black;
            padding: 10px 10px;
    
            a {
                color: #FFFFFF;
                opacity: 1;
                font-size: 13px;
            }
        }
    }

    .header__list.active {
        right: 0;
        margin: 0;
    }

    .header__burger {
        display: block;
        width: 30px;
        height: 20px;
        position: relative;
    
        &:before, &:after {
            content: "";
            background-color: #4579cb;
            position: absolute;
            width: 100%;
            height: 2px;
            transition: all 0.3s ease 0s;
        }
    
        &:before {
            top: 0;
        }
    
        &:after {
            bottom: 0;
        }
    
        span {
            position: absolute;
            background-color: #4579cb;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
            transition: all 0.3s ease 0s;
        }
    
        &.active {
            &::before {
                transform: rotate(45deg);
                top: 9px;
            }
    
            &::after {
                transform: rotate(-45deg);
                bottom: 9px;
            }
            
            span {
                transform: scale(0);
            }
        }
    }

    .header__button {
        display: none;
    }

    .main {
